<template>
  <div class="channel-edit">
    <!-- 我的频道 -->
    <van-cell :border="false">
      <div slot="title" class="title-text">我的频道</div>
      <van-button
        class="edit-btn"
        size="mini"
        @click="isEdit=!isEdit"
        round
        type="danger"
        plain
      >{{isEdit?'完成':'编辑'}}</van-button>
    </van-cell>

    <van-grid class="my-grid" :gutter="10">
      <van-grid-item
        class="grid-item"
        v-for="(channel,index) in myChannel"
        :key="index"
        @click="onMyChannelClick(channel,index)"
      >
        <!-- 控制编辑状态显示 -->
        <van-icon slot="icon" v-show="isEdit && !fixedChannel.includes(channel.id)" name="clear"></van-icon>

        <span class="text" :class="{active:index===active}" slot="text">{{channel.name}}</span>
      </van-grid-item>
    </van-grid>

    <!-- //  频道推荐 -->
    <van-cell :border="false">
      <div slot="title" class="title-text">频道推荐</div>
    </van-cell>
    <van-grid class="recomment-grid" :gutter="10">
      <van-grid-item
        icon="plus"
        class="grid-item"
        v-for="channel in recommendChannel"
        :key="channel.id"
        :text="channel.name"
        @click="onAddChannel(channel)"
      />
    </van-grid>
  </div>
</template>

<script>
import {
  getAllChannels,
  addUserChannel,
  deleteUserChannel
} from "@/api/channel";
import { mapState } from "vuex";
import { setItem } from "@/utils/storage";

export default {
  name: "ChannelEdit",
  props: {
    myChannel: {
      type: Array,
      required: true
    },
    active: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      allChannels: [], //全部频道
      isEdit: false, //控制编辑状态显示
      fixedChannel: [0, 11] //不需要编辑删除的频道
    };
  },
  created() {
    this.loadAllChannels();
  },

  methods: {
    //获取频道
    async loadAllChannels() {
      try {
        const { data } = await getAllChannels();
        console.log("获取所有频道", data);
        this.allChannels = data.data.channels;
      } catch (err) {
        this.$toast("数据获取失败");
      }
    },
    //添加频道
    async onAddChannel(channel) {
      this.myChannel.push(channel);

      // 数据持久化处理
      if (this.user) {
        // 已登录，把数据请求接口放到服务器
        try {
          await addUserChannel({
            id: channel.id, // 频道 ID
            seq: this.myChannel.length // 序号
          });
        } catch (error) {
          this.$toast("保存失败，请稍后再试");
          console.log(error);
        }
      } else {
        // 未登录，把数据存储到本地
        setItem("TOUTIAO_CHANNELS", this.myChannel);
      }
    },
    // 切换频道
    onMyChannelClick(channel, index) {
      if (this.isEdit) {
        // 编辑状态，执行删除频道
        //如果是固定频道就不删除
        if (this.fixedChannel.includes(channel.id)) {
          return;
        }

        //删除频道
        this.myChannel.splice(index, 1);

        // 如果要删除的激活频道之前的频道，则更新激活的频道项
        // 参数1：要删除的元素的开始索引
        // 参数2：要删除的个数，如果不指定，则从参数 1 开始一直删除
        if (index <= this.active) {
          //让激活频道索引-1
          this.$emit("update-active", this.active - 1, true);
        }

        //处理持久化
        this.deleteChannel(channel);
      } else {
        // 非编辑状态，指向切换频道
        this.$emit("update-active", index, false);
      }
    },

    // 在 deleteChannel 处理未登录时候的逻辑
   // 删除频道
   async deleteChannel(channel) {
     try {
       if (this.user) {
         await deleteUserChannel(channel.id)
       } else {
         // 未登录，将数据更新到本地
         setItem('TOUTIAO_CHANNELS', this.myChannels)
       }
     } catch (error) {
       this.$toast('操作失败，请稍后重试')
     }
   }
  },
  computed: {
    ...mapState(["user"]),
    recommendChannel() {
      // 数组的 filter 方法：遍历数组，把符合条件的元素存储到新数组中
      return this.allChannels.filter(channel => {
        // 数组的 find 方法：遍历数组，把符合条件的第一个元素返回
        return !this.myChannel.find(userChannel => {
          return userChannel.id === channel.id;
        });
      });
    }
  }
};
</script>

<style lang="less" scoped>
.channel-edit {
  padding: 85px 0;

  .title-text {
    font-size: 32px;
    color: #333;
  }

  .edit-btn {
    width: 104px;
    height: 48px;
    font-size: 26px;
    color: #f85959;
    border: 1px solid #f85959;
  }

  /deep/ .grid-item {
    width: 160px;
    height: 86px;
    .van-grid-item__content {
      white-space: nowrap;
      background-color: #f4f5f6;

      .van-grid-item__text,
      .text {
        font-size: 22px;
        color: #222;
        margin-top: 4px !important;
      }
      .active {
        color: red;
      }

      .van-grid-item__icon-wrapper {
        position: unset;
      }
    }
  }

  /deep/ .my-grid {
    .grid-item {
      .van-icon-clear {
        position: absolute;
        right: -10px;
        top: -10px;
        font-size: 30px;
        color: #cacaca;
        z-index: 2;
      }
    }
  }

  /deep/ .recomment-grid {
    .grid-item {
      width: 160px;
      height: 86px;
      .van-grid-item__content {
        flex-direction: row;

        .van-icon-plus {
          font-size: 22px;
          line-height: 22px;
          margin-right: 6px;
        }
      }
    }
  }
}
</style>

  