<template>
  <div class="home-container">
    <!-- 头部导航栏组件 -->
    <van-nav-bar class="page-nav-bar" fixed="">
      <van-button class="search-btn" 
      slot="title" type="info" 
      size="small" round icon="search"
      :to="{name: 'search'}"
      >搜索</van-button>
    </van-nav-bar>

    <!-- 频道列表 -->
    <van-tabs class="channel-tabs" v-model="active" animated swipeable>
      <van-tab
      v-for="channel in channel"
      :key="channel.id"
       :title="channel.name">

       <!-- 文章列表 -->
      <ArtcleList :channel="channel"></ArtcleList>
       <!-- 文章列表 -->
       </van-tab>
     

      <!-- 汉堡按钮 -->
     <div slot="nav-right" class="placeholder"></div>
     <div slot="nav-right" class="hamburger-btn" @click="isChannelEditShow=true">
       <i class="toutiao toutiao-gengduo"></i>
     </div>
    </van-tabs>
    
    <van-popup
  v-model="isChannelEditShow"
    close-icon-position="top-left"
  closeable
  position="right"
  :style="{ height: '100%' }"
  >
  <ChannelEdit :active="active" @update-active="onUpdateActive" :myChannel="channel"></ChannelEdit>
  </van-popup>
  </div>
</template>

<script>
import { getUserChannel } from '@/api/user'
import ArtcleList from './components/artcle-list.vue';
import ChannelEdit from '@/views/home/components/channel-edit.vue'
import { mapState } from 'vuex';
import { getItem } from '@/utils/storage';
export default {
  name: "HomeIndex",
  data() {
    return {
      active: 0,
      channel: [],  //频道列表
      isChannelEditShow:false,// 控制编辑频道弹出层的显示状态
    };
  },
  created() {
    this.loadChannels()
  },
  methods: {
    async loadChannels() {
      // try {
      //   const { data } = await getUserChannel()
      //   console.log('用户选择频道', data);
      //   this.channel=data.data.channels
      // } catch (err) {
      //   this.$toast('获取频道数据失败')
      // }
 let channels = []
   
     if (this.user) {
       // 已登录，请求获取用户频道列表
       const { data } = await getUserChannel()
       channels = data.data.channels
     } else {
       // 未登录，判断是否有本地的频道列表数据
       const localChannels = getItem('TOUTIAO_CHANNELS')
       // 如果存在本地的频道列表数据
       if (localChannels) {
         channels = localChannels
       } else {
         // 没有，请求获取默认频道列表
         const { data } = await getUserChannel()
         channels = data.data.channels
       }
     }
   
     this.channel = channels

    },

// 接收父组件传递数据，实现频道切换
    onUpdateActive(index,isChannelEditShow=true) {
      this.active = index
      this.isChannelEditShow=isChannelEditShow
    }
  },
  components: {
    ArtcleList,
    ChannelEdit
  },
  computed: {
  ...mapState(['user'])
}

};
</script>

<style lang="less" scoped>
.home-container {
  padding-top: 174px;
    padding-bottom: 100px;
  /deep/ .van-nav-bar__title {
    max-width: unset;
  }
  // 导航搜索
  .search-btn {
    width: 555px;
    height: 64px;
    background-color: #5babfb;
    border: none;
    font-size: 28px;
    .van-icon {
      font-size: 32px;
    }
  }

  // 频道列表
 /deep/ .channel-tabs {
    .van-tabs__wrap {
      position: fixed;
      top: 92px;
      z-index: 1;
       left: 0;
       right: 0;
      height: 82px;
    }

    .van-tab {
      min-width: 200px;
      border-right: 1px solid #edeff3;
      font-size: 30px;
      color: #777777;
    }

    .van-tab--active {
      color: #333333;
    }

    .van-tabs__nav {
      padding-bottom: 0;
    }
    
    .van-tabs__line {
      bottom: 8px;
      width: 31px !important;
      height: 6px;
      background-color: #3296fa;
    }

   .placeholder {
     flex-shrink: 0;
     width: 66px;
     height: 82px;
   }
    // 汉堡按钮
    .hamburger-btn{
      position: fixed;
      right: 0;
      display: flex;
      justify-content: center;
      align-items:center;
      width: 66px;
      height: 82px;
      background-color: #fff;
      opacity: 0.9;

      i.toutiao{
        font-size: 33px;
      }
      &:before{
        content: '';
        position: absolute;
        left: 0;
        width: 1px;
        height: 100%;
        background-image: url(~@/assets/gradient-gray-line.png);
        background-size: contain;
      }
    }
  }
}
</style>